#section-to-print {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 90vw;
  background-color: #EEEEEE;
  margin: 0 auto;
  padding: 1em 0;
}


.file-container {
  display: inline-block;
  position: relative;
  width: 250px;
}

.mask {
  position: absolute;
  height: 1.5em;
  width: 170px;
  top: -1px;
  left: 87px;
  background-color: white;
  padding-left: 3px;
}
